import React, { useState } from "react";
import Header from "./Header"
import Note from "./Note";
import CreateArea from "./CreateArea";

function App() {

   var keepNotes = []
 
   var storedNotes = JSON.parse(localStorage.getItem('keepNotes'));

   if (storedNotes !== null){
    keepNotes = storedNotes.slice[0];
   }
  
  



  const [notes, setNotes] = useState([]);

  function addNote(newNote) {
    setNotes(prevNotes => {
      keepNotes = [...prevNotes, newNote];
  localStorage.setItem("keepNotes", JSON.stringify(keepNotes));

      return keepNotes;
    });
  }

  function deleteNote(id) {
    setNotes(prevNotes => {
      keepNotes = prevNotes.filter((noteItem, index) => {
        return index !== id;
      });
      localStorage.setItem("keepNotes", JSON.stringify(keepNotes));
      return keepNotes
    });
  }

  return (
    <div>
      <Header />
      <CreateArea onAdd={addNote} />
      {notes.map((noteItem, index) => {
        return (

            <Note
              key={index}
              id={index}
              title={noteItem.title}
              content={noteItem.content}
              onDelete={deleteNote}
            />
        );
      })}

    </div>
  );
}

export default App;
